				<input type="text" required v-model="name"/><label>用户名</label>
			</div>
			<div class="login_box">
				<input type="password" required="required" @keyup.enter="click" v-model="password"/><label>密码</label>
			</div>
            <button type="primary"  @click="click">点击登录<span></span>
				<span></span>
				<span></span>
				<span></span></button>
                 <button type="success" style="margin:15px 0 0 0"  @click="zhuce">点击注册</button>
			<!-- <a href="javascript:void(0)">
				登录
				<span></span>
				<span></span>
				<span></span>
				<span></span>
			</a> -->
		</div>
	</body>
  
</template>

<script>
