const ax=axios.create({
        baseURL:'http://119.3.182.104:8080/crop/users/login'
    })

export default {
    
    name:'denglu',
    data(){
        return {
            name:'',
            password:'',
            
        }
    },
    methods: {
        click(){
            const _this=this;
            if(this.name===''){
                ax({
                method:'GET',
                

        }).then(res=>{
            this.title=res.data.message
            console.log(_this);
            alert(res.data.message)
